<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Alta de Accesorios
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="8"
      >
        <form @submit.prevent="guardarAccesorio()">
          <v-text-field
            v-model="props.name"
            label="Accesorio"
            outlined
            prepend-icon="mdi-account-tie"
            required
          />
          <v-text-field
            v-model="props.type"
            label="Tipo"
            outlined
            prepend-icon="mdi-label"
            required
          />
          <v-text-field
            v-model="props.forCar"
            label="Vehiculo"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="props.cantidad"
            label="Cantidad"
            type="number"
            outlined
            prepend-icon="mdi-lock"
            required
          />
          <v-text-field
            v-model="props.precio"
            label="Precio"
            prepend-icon="mdi-cash"
            prefix="$"
            outlined
            required
          />

          <v-card-actions>
            <v-btn
              to="/ventas/accesorios"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
  import axios from 'axios'

  const urlAccesorio = 'https://sistema-adv.herokuapp.com/api/props/'

  export default {
    name: 'CrearAccesorio',
    data () {
      return {
        props: {
          name: '',
          type: '',
          forCar: '',
          cantidad: '',
          precio: '',
        },
      }
    },
    mounted () {},
    methods: {
      guardarAccesorio () {
        const router = this.$router
        const params = this.props
        axios.post(urlAccesorio, params).then((res) => {
          // console.log(res);
          router.push('/ventas/accesorios')
          this.limpiarCampos()
        })
      },
      limpiarCampos () {
        this.props = ''
      },
    },
  }
</script>
